nav {
    width: 100%;
    font-size: 3rem;
    color: #fff;
    margin-bottom: 50px;
    background-color: rgb(56,56,56);
    height: auto;
    font-family: 'Luckiest Guy', cursive;
}

a {
    color: #2080fe;
}

a:hover {
    color: #2080fe;
    outline-color: #fe2020;
    text-decoration: none;
}

nav li {
    display: inline-block;
    padding: 26px 0;
    width: 25%;
}

ul {
    display: flex;
    align-items: center;
}

@media only screen and (max-width: 750px) {
    #cur-sco, #top-sco {
        font-size: 2rem;
    }
}